import "./App.css";
import { useState, useEffect } from "react";
import { collection, getDocs } from "firebase/firestore";

import { db } from "./Data/data";

function App() {
  const [pDetails, setPDetails] = useState([]);
  const [pADetails, setPADetails] = useState([]);
  const [pSDetails, setPSDetails] = useState([]);
  const [pEDetails, setPEDetails] = useState([]);
  const [loading, setLoading] = useState(true);

  const DB = collection(db, "PDetails");
  const ADB = collection(db, "PAbout");
  const SDB = collection(db, "PSkills");
  const EDB = collection(db, "PExp");
  const getPData = async () => {
    const data = await getDocs(DB);
    const adb = await getDocs(ADB);
    const sdb = await getDocs(SDB);
    const edb = await getDocs(EDB);

    let newdata = data.docs.map((da) => ({ ...da.data(), id: da.id }));
    setPDetails(newdata[0]);
    setPADetails(adb.docs.map((da) => ({ ...da.data(), id: da.id }))[0]);

    setPEDetails(edb.docs.map((da) => ({ ...da.data(), id: da.id })));

    let pSkils = sdb.docs.map((da) => ({ ...da.data(), id: da.id }));
    setPSDetails(pSkils);
    setLoading(false);
  };
  useEffect(() => {
    getPData();
    return () => {
      console.log("");
    };
  }, []);
  return (
    <>
      {!loading ? (
        <div className="MAIN">
          {/* <h1>HanU</h1> */}
          <section>
            <div className="pra-contact">
              <span className="pra-section">
                <span className="pra-img">
                  <img src={pDetails["img"]} />
                </span>
                <span className="pra-name">
                  <h3>{pDetails["Fname"] + " " + pDetails["Lname"]}</h3>
                </span>
              </span>
              <span className="pra-det">
                <div className="cont">
                  <span className="pra-icons">
                    <a href={`tel:+91${pDetails["phone"]}`}>
                      <span className="material-icons ">phone</span>
                    </a>
                  </span>
                  <span>
                    <h5 className="pra-sel-us">{`${pDetails["phone"]}`}</h5>
                  </span>
                </div>
                <div className="cont">
                  <span className="pra-icons">
                    <a href={`mailto:${pDetails["email"]}`}>
                      <span className="material-icons ">email</span>
                    </a>
                  </span>
                  <span>
                    <h5 className="pra-sel-us">{`${pDetails["email"]}`}</h5>
                  </span>
                </div>
              </span>
            </div>
            <div className="spacer"></div>
            <div className="pra-contact pr-flex-direction-col pr-aline-start">
              <div>
                <h4>About Me:</h4>
              </div>
              <div>
                <p>{pADetails.about}</p>
              </div>
            </div>
            <div className="spacer"></div>
            <div className="pra-contact pr-flex-direction-col pr-remove-padd pr-aline-start">
              <div>
                <h4>Skills:</h4>
              </div>
              <div className="pr-scroll pr-scroll-x">
                <div className="pr-skill-container">
                  {pSDetails.sort().map((skl) => (
                    <span key={skl.id} className="pra-skill pra-box-shadow ">
                      <div>
                        <img src={skl.img} alt={skl.skill} />
                      </div>
                      <div>
                        <h5>{skl.skill}</h5>
                      </div>
                    </span>
                  ))}
                </div>
              </div>
            </div>
            <div className="spacer"></div>
            <div className="pra-contact pr-flex-direction-col pr-aline-start ">
              <span className="pr-flex-direction-col ">
                <h4>Professional Experience:</h4>
              </span>
              <span className="pr-flex-direction-col">
                {pEDetails.map((exp) => (
                  <div key={exp.id} className="pr-aline-center">
                    <span className="pr-li-"></span>
                    <h4>{exp.exp}</h4>
                  </div>
                ))}
              </span>
            </div>
          </section>
        </div>
      ) : (
        <div className="MAIN">
          <svg
            width="100%"
            height="100%"
            viewBox="50 -10 75 100"
            style={{
              fillRule: "evenodd",
              clipRule: "evenodd",
              strokeLinejoin: "round",
              strokeMiterlimit: 1.41421,
            }}
          >
            <defs>
              <mask id="mask-element">
                <path
                  fill="#777"
                  d="M283,18.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-183.5,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l183.5,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
                />

                <path
                  fill="#777"
                  d="M283,28.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-183.5,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l183.5,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
                />

                <path
                  fill="#777"
                  d="M254,38.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-154.5,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l154.5,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
                />

                <path
                  fill="#777"
                  d="M281.75,48.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-182.25,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l182.25,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
                />

                <path
                  id="qube"
                  fill="#777"
                  d="M92,20.87c0,-1.86 -1.51,-3.37 -3.37,-3.37l-28.26,0c-1.86,0 -3.37,1.51 -3.37,3.37l0,28.26c0,1.86 1.51,3.37 3.37,3.37l28.26,0c1.86,0 3.37,-1.51 3.37,-3.37l0,-28.26Z"
                />

                <path
                  fill="hsla(200,0%,10%,.6)"
                  id="mask"
                  d="M52,17.5l0,35l-40,0l20,-35l20,0Z"
                />
              </mask>
            </defs>

            <path
              mask="url(#mask-element)"
              d="M283,18.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-183.5,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l183.5,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
              fill="#dadada"
            />

            <path
              mask="url(#mask-element)"
              d="M283,28.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-183.5,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l183.5,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
              fill="#dadada"
            />

            <path
              mask="url(#mask-element)"
              d="M254,38.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-154.5,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l154.5,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
              fill="#dadada"
            />

            <path
              mask="url(#mask-element)"
              d="M281.75,48.75c0,-0.69 -0.56,-1.25 -1.25,-1.25l-182.25,0c-0.69,0 -1.25,0.56 -1.25,1.25l0,2.5c0,0.69 0.56,1.25 1.25,1.25l182.25,0c0.69,0 1.25,-0.56 1.25,-1.25l0,-2.5Z"
              fill="#dadada"
            />

            <path
              mask="url(#mask-element)"
              id="qube"
              d="M92,20.87c0,-1.86 -1.51,-3.37 -3.37,-3.37l-28.26,0c-1.86,0 -3.37,1.51 -3.37,3.37l0,28.26c0,1.86 1.51,3.37 3.37,3.37l28.26,0c1.86,0 3.37,-1.51 3.37,-3.37l0,-28.26Z"
              fill="#dadada"
            />
          </svg>
        </div>
      )}
    </>
  );
}

export default App;
